
.header {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 70px;
  background: #5c5e63;
  color: white;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.header i {
    position: absolute;
    z-index: 1;
    display: inline-block;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    width: 25px;
    height: 25px;
}
.header i.icon-menu {
    left: 20px;
    top: 25px;
    background: url("../../common/icons/icon-menu.svg") no-repeat;
    background-size: contain;
}
.header i.icon-msg {
    right: 55px;
    top: 23px;
    background: url("../../common/icons/icon-msg.svg") no-repeat;
    background-size: contain;
    cursor: pointer;
}
.header span.msg-count {
    position: absolute;
    z-index: 1;
    width: 15px;
    height: 15px;
    right: 45px;
    top: 18px;
    text-align: center;
    line-height: 15px;
    color: #fff;
    background-color: #ff4081;
    border-radius: 50%;
    font-size: 60%;
}
.header h1 {
    font-size: 30px;
    letter-spacing: 2px;
    font-weight: 700;
    font-style: oblique;
}
.header .singout {
    position: absolute;
    right: 8px;
    top: 28px;
    cursor: pointer;
}



