
.article-list {
  position: relative;
  z-index: 1;
}
.article-list .item {
    padding-left: 2%;
    width: 100%;
    height: 60px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.article-list .item .avatar {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 50px;
              flex: 0 0 50px;
      padding: 10px;
}
.article-list .item .avatar img {
        width: 40px;
        height: 40px;
        border-radius: 5px;
}
.article-list .item .title {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      background-color: white;
      font-size: 15px;
      overflow: hidden;
      padding-top: 15px;
}
.article-list .item .title .text {
        width: 90%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
}
.article-list .item .title .text .flag {
          background-color: #e5e5e5;
          font-size: 80%;
          padding: 4px;
          border-radius: 8px;
          margin-right: 4px;
}
.article-list .item .title .text .special {
          background-color: #80db01;
          color: white;
}
.article-list .item .title .text a {
          color: #000;
}
.article-list .item .title .text a:hover {
            text-decoration: underline;
            line-height: 1rem;
}
.article-list .item .title .view {
        width: 95%;
        position: relative;
        font-size: 85%;
        color: gray;
        margin-top: 10px;
        padding-left: 40px;
}
.article-list .item .title .view .date {
          position: absolute;
          right: 12%;
}
@media screen and (min-width: 760px) {
.article-list .item {
      margin: auto;
      width: 70%;
}
}
.article-list .more {
    text-align: center;
    height: 50px;
}
.article-list .more .icon-more {
      display: inline-block;
      background: url("../../common/icons/icon-loading.svg") no-repeat;
      background-size: contain;
      width: 30px;
      height: 30px;
      margin: 10px auto;
      -webkit-animation: loading 5s linear infinite;
              animation: loading 5s linear infinite;
}



