
.aside-menu {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
.aside-menu .menu {
    width: 150px;
    background-color: #5c5e63;
    opacity: .9;
    color: #eee;
    padding-top: 60px;
}
.aside-menu .menu .user {
      width: 110px;
      padding-bottom: 10px;
      margin-left: 25px;
      border-bottom: 2px solid #fff;
      cursor: pointer;
}
.aside-menu .menu .user .user-avatar img {
        width: 30px;
        height: 30px;
}
.aside-menu .menu .user .username {
        font-size: 18px;
        position: relative;
        top: -8px;
        left: 15px;
}
.aside-menu .menu .infos {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      margin-top: 20px;
}
.aside-menu .menu .infos .block {
        height: 50px;
        text-align: center;
        border-bottom: 1px solid #ccc;
        line-height: 50px;
        cursor: pointer;
}
.aside-menu .menu .infos .about {
        border: none;
        letter-spacing: 8px;
}
.aside-menu .menu .infos i {
        position: relative;
        top: 5px;
        display: inline-block;
        width: 22px;
        height: 22px;
        background-repeat: no-repeat;
        margin-right: 8px;
}
.aside-menu .menu .infos .icon-msg {
        background: url("../../common/icons/icon-msg.svg");
        background-size: contain;
}
.aside-menu .menu .infos .icon-new {
        background: url("../../common/icons/icon-new.svg");
        background-size: contain;
}
.aside-menu .menu .infos .icon-info {
        background: url("../../common/icons/icon-info.svg");
        background-size: contain;
}
.aside-menu .menu .infos .icon-about {
        background: url("../../common/icons/icon-about.svg");
        background-size: contain;
        margin-right: 20px;
}
.aside-menu .mask {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    z-index: 40;
}



